<style scoped>
.sc-container{
    padding: 31px 40px 24px 40px;
    background:white;
}
.sc-tag{
    opacity: 0.5;
}
.sc-question-container{
    line-height:32px;
}
.sc-question-title{
    margin-left:33px;
}
.sc-question-content{
    margin-left:32px;
    opacity: 0.6;
}
</style>
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  props:{
      mainData:Object
  },
  created(){
     
  }
}
</script>





