<template>
  <div class="submit-question-container">
    <div class="submit-question incenter">
      <div class="font20pl3a3c50 mb16 sqc-title">提问</div>
      <div class=" submit-li">
        <div class="floatl submit-label">标题：</div>
        <div class="mb32 ml">
          <el-form :model="title">
            <el-form-item>
              <el-input v-model="title" placeholder="请一句话简介明了的说明你的问题"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="sqc-tip-container ml submit-li">
        <div class="sqct-usericon floatl">
          <img class="sqct-icon" src="../../assets/img/user-icon.jpg" alt="">
        </div>
        <div class="sqct-content font14pr424242">
          在你提问之前，不妨先搜索一下你的问题，其他同学可能问到过同样的问题，应该尽量确保你的问题是独一无二的。
        </div>
      </div>
      <div class="mb16 clearfix submit-li">
        <div class="floatl submit-label">问题描述：</div>
        <div class="ml">
          <quill-editor v-model="content"  ref="myQuillEditor">
          </quill-editor>
        </div>
      </div>
      <div class="relative mb16 submit-li">
        <div class="floatl submit-label">问题方向：</div>
        <div class="ml">
          <el-button v-for="(item,index) in tags" :key="index" @click="selectDirection(item)">{{item.name}}</el-button>
        </div>
      </div>
      <div class=" submit-li">
        <div class="floatl submit-label">问题描述：</div>
        <div class="mb32 ml">
          <el-select v-model="reward" placeholder="活动区域">
            <el-option v-for="(item,index) in arr" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
      </div>
      <div class="sq-submit">
        <el-button @click="submitQuestion()">tijiao</el-button>
        <el-button>quxiao</el-button>
      </div>
    </div>

  </div>
</template>
<!-- Add "scoped" attribute to limit CSS to this component only -->

<script>
  import Vue from 'vue'
  import VueQuillEditor from 'vue-quill-editor'
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
  Vue.use(VueQuillEditor)
  export default {
    name: 'HelloWorld',
    data() {
      return {
        title: '',
        content: '',
        direction: '',
        reward: 0,
        tags: [],
        arr: [{
          label: '不悬赏',
          value: 0
        }, {
          label: '1积分',
          value: 1
        }, {
          label: '2积分',
          value: 2
        }, {
          label: '3积分',
          value: 3
        }, {
          label: '4积分',
          value: 4
        }, {
          label: '5积分',
          value: 5
        }],
        options: {
          modules: {
            toolbarOptions: [
              ['bold', 'italic', 'underline'], // toggled buttons
              ['blockquote', 'code-block'],
              [{
                'list': 'ordered'
              }, {
                'list': 'bullet'
              }], // text direction
              [ {
                'background': []
              }], // dropdown with defaults from theme
              [{
                'align': []
              }],
            ]
          }
        }
      }
    },
    methods: {
      submitQuestion() {
        this.$post('/community/add/faq', {
          video_id: this.$fn.funcUrl('video_id'),
          custom_user_id: localStorage.uid,
          title: this.title,
          description: this.content,
          path: this.direction,
          reward: this.reward
        }).then(res => {
          console.log(res)
        })
      },
      selectDirection(item) {
        this.direction = item.id;
      }
    },
    created() {
      this.$get('/tracks/question/path/info').then(res => {
        this.tags = res.data.data
      })
    },
    mounted() {}
  }

</script>

<style scoped>
  .relative {
    position: relative;
  }

  .submit-question-container {
    background: #fafafa;
    padding-top: 40px;
    height: 1000px;
  }

  .sqc-title {
    margin-bottom: 34px;
  }

  .submit-li {
    position: relative
  }

  .submit-label {
    width: 70px;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    text-align: right;
  }

  .sqc-tip-container {
    margin-bottom: 34px;
    position: relative;
  }

  .sqct-usericon {
    position: absolute;
    top: 50%;
    margin-top: -12px;
  }

  .sqct-icon {
    height: 24px;
    width: 24px;
    vertical-align: middle;
  }

  .ml {
    margin-left: 94px;
  }

  .sqct-content {
    opacity: 0.5;
    margin-left: 30px;
  }

  .submit-question {
    background: white;
    width: 649px;
    box-sizing: border-box;
    padding: 16px 40px 24px 40px;
  }

  .sq-submit {
    text-align: right;
  }

  .mb32 {
    margin-bottom: 32px;
  }

  .mb16 {
    margin-bottom: 16px;
  }

</style>



